import React from "react";
import clsx from "clsx";
import * as st from "./description-block.module.css";

export const DescriptionBlock = ({
  heading,
  value,
  noTopMargin,
  price,
  className,
  style,
  ...rest
}) => {
  const descriptionBlockStyle = {
    marginTop: !noTopMargin && "1rem",
  };

  return (
    <div
      {...rest}
      style={{ ...descriptionBlockStyle, ...style }}
      className={clsx(className)}
    >
      <p className={st.heading}>{heading}</p>
      <p className={price ? st.price : st.text}>
        {price ? `${value}₽` : value}
      </p>
    </div>
  );
};
