import React from "react";
import clsx from "clsx";
import * as st from "./product.module.css";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Zoom } from "swiper";
import { DescriptionBlock } from "src/components/ui/description-block";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/zoom";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { usePurchasesActions } from "src/contexts/purchases-context";
import { BasketButton } from "src/components/ui/basket-button";

export const Product = ({ product, className, style, ...rest }) => {
  const { name, sku, price, specs, description, additionalInfo, images } =
    product;
  const { addPurchase } = usePurchasesActions();

  const addToCart = () => {
    addPurchase(1);
  };

  return (
    <div style={{ ...style }} className={clsx(st.product, className)} {...rest}>
      <div className={st.pane__left}>
        <h2 className={st.product__name}>{name}</h2>
        <Swiper
          navigation={images.length > 1}
          zoom={true}
          pagination={{
            dynamicBullets: false,
            clickable: true,
          }}
          modules={[Navigation, Pagination, Zoom]}
          className={st.swiper}
          cssMode={true}
        >
          {images.map((image, idx) => (
            <SwiperSlide key={idx}>
              <div className="swiper-zoom-container">
                <GatsbyImage
                  className={clsx("swiper-zoom-target", st.swiper__image)}
                  image={getImage(image)}
                  alt={name}
                />
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>

      <div className={st.divider}></div>
      <div className={st.pane__right}>
        <p className={st.product__sku}>
          Артикул: <span className={st.sku__value}>{sku}</span>
        </p>
        <div className={st.product__mainSpecsBlock}>
          <DescriptionBlock
            noTopMargin
            heading="Технические характеристики"
            value={specs}
          />
          <div className={st.product__priceBlock}>
            <DescriptionBlock noTopMargin price heading="Цена" value={price} />
            <BasketButton
              className={st.product__basket}
              value="+"
              type="product"
              onClick={addToCart}
            />
          </div>
        </div>
        <DescriptionBlock noTopMargin heading="Описание" value={description} />
        {additionalInfo && (
          <DescriptionBlock
            heading="Дополнительная информация"
            value={additionalInfo}
          />
        )}
      </div>
    </div>
  );
};
